* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Jost", sans-serif;
}


a {
  text-decoration: none;
  /* font-size: 2.5rem; */
  z-index: 99;
}

li {
  list-style: none;
  font-size: 2.5rem;
  z-index: 99;
  /* background-color:red; */
}

/* ---------------  navbar style start  --------- */

.main-nav {
  width: 100%;
  height: 10rem;
  display: grid;
  grid-template-columns: 4rem 1fr 2fr 3fr 4rem;
  
}
.main-nav-homepage {
  background-color: transparent;
  position: absolute;
  z-index: +1;
  top:0;
  width: 100%;
  height: 10rem;
  display: grid;
  grid-template-columns: 4rem 1fr 2fr 3fr 4rem;
  
  background-color: transparent;
  position: absolute;
  z-index: +1;
  top:0;
}
svg {
  color: white;
  z-index: 99;
}
.logo {
  -webkit-filter: drop-shadow(0px 0px 12px #dde6f3);
          filter: drop-shadow(0px 0px 12px #dde6f3);
  display: grid;
  /* background-color: #3b5998; */
  grid-column: 2/2;
  justify-content: start;
  align-items: center;
  z-index: 99;
}
.hamburger-menu {
  display: none;
  /* color: antiquewhite; */
  z-index: 99;
}

.leader_rotLogo-image {

  height: 12vw;
  width: 15vw;
  z-index: 99;
}
.menu-link {
  grid-column: 3/5;
  z-index: 99;
}

.menu-link ul {
  height: 10rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  z-index: 99;
}

.social-media {
  grid-column: 4/5;
  z-index: 99;
}

/* .social-media ul {
  height: 10rem;
  display: grid;
  grid-template-columns: 3fr repeat(3, 1fr);
  align-items: center;
  justify-content: flex-end;
} */

/* .social-media ul li {
  text-align: right;
} */

/* .social-media ul li:first-child {
  grid-column: 2/3;
} */

/* ----------- Grid part ends ----------------- */

.menu-link ul li {
  font-size: 1.2rem;
  z-index: 99;
}

.menu-link ul li a {
  text-transform: capitalize;
  /* color: rgba(62, 62, 172, 0.8); */
  color: white;
  transition: 0.5s;
  z-index: 99;
}
.menu-link ul li a:hover {
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
  /* background: black; */
  box-shadow: 0px 0px 10px 7px rgba(0, 0, 0, 0.1);
}
.menu-link ul li:hover > a {
  -webkit-transform-origin: left;
          transform-origin: left;
  /* color: rgba(0, 0, 3, 1); */
  color: white;
  text-decoration: underline;
  transition: 0.5s;
  z-index: 99;
}

/* ------------------------responsive css style -------------------------------- */

@media (max-width: 1070px) {
  .main-nav,.main-nav-homepage{
    /* height: 7rem; */
    grid-template-columns: 2rem 1fr 4fr 1rem;
  }

  .logo,
  .menu-link ul {
    height: 8rem;
  }
  a {
    text-decoration: none;
    /* font-size: 2.5rem; */
  }
}
.hamburger-menu {
  display: none;
  /* color: antiquewhite; */
  z-index: 99;
}

/* responsive css style  */

@media (max-width: 790px) {
  .main-nav, .main-nav-homepage{
    height: 7rem;
    grid-template-columns: 2rem 2fr 3fr 2rem 2rem;
  }

  .menu-link {
    display: none;
  }

  .logo,
  .social-media {
    height: 7rem;
  }

  .mobile-menu-link {
    grid-column: 2/4;
    /* position: relative; */
    /* z-index: 99; */
  }

  .mobile-menu-link {
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 50;
    background-color: black;
  
    min-height: 90vh;
    min-width: 90vw;
    display: grid;
    justify-content: center;
    grid-column: 2/5;
    align-items: center;
    padding-left: 3rem;
    transition: all 2s linear;
    -webkit-transform-origin: top;
            transform-origin: top;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
      rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  }

  .mobile-menu-link ul {
    height: calc(10rem + 10vh);
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: start;
  }

  .mobile-menu-link ul li:first-child {
    transition-delay: 0.2s;
  }

  .social-media {
    grid-row: 1/2;
    grid-column: 3/5;
    justify-items: end;
    align-items: center;
    transition: all 2s linear;
  }

  .social-media .social-media-desktop {
    height: 0;
    display: none;
  }

  .social-media {
    height: 7rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
    z-index: 99;
  }
  a {
   
    font-size: calc(1.5rem + 4vw);
  }
  .menu-link ul li a {
    color: aliceblue;
    /* font-size : 2rem; */
  }
  .leader_rotLogo-image {

    height: 16vw;
    width: 19vw;
    z-index: 99;
  }
  .menu-link ul li a{
    color: #ADD7F2;
  }
}

@media (max-width: 520px) {
  .main-nav,.main-nav-homepage{
    height: 6rem;
    grid-template-columns: 1rem 4fr 1fr 1fr 1rem;
    color: aliceblue;
  }

  .logo,
  .social-media ul {
    height: 6rem;
  }

  .logo h2 {
    font-size: 2rem;
  }

  .social-media {
    height: 6rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }

  .menu-link ul li a {
    color: aliceblue;
    font-size: calc(1.5rem + 2vw);
  }
  .leader_rotLogo-image {
    height: 23vw;
    width: 25vw;
    z-index: 99;
  }
  .menu-link ul li a{
    color: #ADD7F2;
  }
}

.event_main{
    /* background:linear-gradient(3.97deg, #0066FF 2.93%, #000917 96.58%) fixed; */
    min-width: 100vw;
    min-height: 100vh;    
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}   
.event_header{
    font-family: Bebas Neue;
    font-style: normal;
    font-weight: 400;   
    text-transform: capitalize;
    font-size: calc(12px + 6vw);
    margin-top: 30vh;
    margin-bottom: 20vh;
    color: #ADD7F2;
}
.evnt_main_con{
    display: flex;
    flex-wrap: wrap;
    width: 90%;
    justify-content: space-evenly;
}
.event{
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: clamp(10px,25px,30px);
    backdrop-filter: blur(15px) saturate(120%);
    -webkit-backdrop-filter: blur(16px) saturate(200%);
    background-color: rgba(255, 255, 255, 0.18);
    border: rgba(186,186,186,0.4) solid 1.5px;
    padding: 20px;
    margin: 0 20px 20px 0;
    /* font-family: Inter; */
    font-size: 1.2rem;
    width: 30%;
    color: #000917;
    font-family: 'Montserrat', sans-serif !important;
}
.event:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0px 15px 10px rgba(255, 255, 255, 0.1);
    transition: 300ms ease-in;
    background-color: rgba(255, 255, 255, 0.20)
}

.event_img{
    max-height: 300px;
    width: 100%;
    background: #000917;
    border-radius: clamp(10px,25px,30px);
    object-fit: fill;
}
.title{
    font-weight: 600;
    text-align: left;
    align-self: flex-start;
    font-size: 1.7rem;
    margin-top: 0.5vw;
    margin-bottom: 0.2vw;
}
.description{
    height: 290px;
    overflow: hidden;
}
/* Smartphones ----------- */
@media only screen
and (max-width: 800px) {
    .event_header{
        font-size: 4rem;
    }
    .event{
